
@mixin hide-text {
    text-indent: 101%;
    overflow: hidden;
    white-space: nowrap;
}

@mixin nav-link-hover{
    position: relative;
    display: block;
    transition: all 0.2s ease-in-out;

    &:after {
        content: "";
        width: 100%;
        height: 0.5rem;
        position: absolute;
        bottom: 0;
        left: 0;
        background-color: $white;
        transition: all 0.2s ease-in-out;
        transform: scaleX(0) translate3d(0, 0, 0);
    }

    &:hover:after {
        transform: scaleX(1);
    }
}

@mixin absolute-center{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@mixin pc{
    @media (max-width: 1024px){
        @content
    }
};
@mixin pad{
    @media (max-width: 768px){
        @content
    }
};
@mixin mobile{
    @media (max-width: 414px){
        @content
    }
};
